"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { Venda, vendaService } from '@/services/api/venda.service';
import VendaCard from '@/components/vendas/VendaCard';
import { Modal } from '@/components/ui/modal';
import VendaForm from '@/components/vendas/VendaForm';
import Button from '@/components/ui/Button';
import Input from '@/components/form/input/InputField';
import Pagination from '@/components/ui/Pagination';
import Label from '@/components/form/Label';

export default function VendasPage() {
  const router = useRouter();
  const [vendas, setVendas] = useState<Venda[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedVenda, setSelectedVenda] = useState<Venda | null>(null);
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadVendas = useCallback(async () => {
    try {
      setLoading(true);
      const response = await vendaService.list({ 
        page, 
        limit: itemsPerPage, 
        search: searchTerm 
      });
      setVendas(response.data);
      // Assumindo que a API retorna informa√ß√µes de pagina√ß√£o
      if (response.pagination) {
        setTotalPages(response.pagination.pages);
      } else {
        // Fallback se n√£o houver pagination information
        setTotalPages(Math.ceil((response.data?.length || 0) / itemsPerPage));
      }
    } catch (error) {
      toast.error('Erro ao carregar vendas');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadVendas();
  }, [loadVendas]);

  const handleEdit = (venda: Venda) => {
    setSelectedVenda(venda);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta venda?')) return;

    try {
      await vendaService.delete(id);
      toast.success('Venda exclu√≠da com sucesso');
      loadVendas();
    } catch (error) {
      toast.error('Erro ao excluir venda');
    }
  };

  const handleViewInvoices = (vendaId: number) => {
    router.push(`/dashboard/empresa/financeiro/vendas/vendas-finais/${vendaId}`);
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedVenda) {
        await vendaService.update(selectedVenda.id, data);
        toast.success('Venda atualizada com sucesso');
      } else {
        await vendaService.create(data);
        toast.success('Venda criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedVenda(null);
      loadVendas();
    } catch (error) {
      toast.error('Erro ao salvar venda');
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Vendas
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie suas vendas e contratos
            </p>
          </div>
          <Button
            variant="primary"
            onClick={() => {
              setSelectedVenda(null);
              setIsModalOpen(true);
            }}
          >
            Nova Venda
          </Button>
        </div>

        <div className="mt-6">
          <div className="flex flex-col sm:flex-row gap-4 items-center justify-between mb-6">
            <Input
              placeholder="Buscar vendas..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setPage(1);
              }}
              className="max-w-xs"
            />
          </div>

          <div className="flex justify-between mb-4 gap-2 flex-wrap">
            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por p√°gina</Label>
              <Input
                className='max-w-24'
                defaultValue={itemsPerPage}
                max={100}
                min={1}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  const newValue = Number(e.target.value) || 9;
                  setItemsPerPage(newValue);
                  setPage(1);
                }}
              />
            </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {vendas.length > 0 ? (
            vendas.map((venda) => (
              <VendaCard
                key={venda.id}
                venda={venda}
                onEdit={() => handleEdit(venda)}
                onDelete={() => handleDelete(venda.id)}
                onViewInvoices={() => handleViewInvoices(venda.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma venda encontrada.
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedVenda(null);
        }}
        className="max-w-[900px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedVenda ? 'Editar Venda' : 'Nova Venda'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedVenda 
                ? 'Atualize as informa√ß√µes da venda.' 
                : 'Preencha os dados para criar uma nova venda.'
              }
            </p>
          </div>
          <VendaForm
            venda={selectedVenda}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedVenda(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
