import React, { useState } from 'react';
import { Venda, StatusPagamento } from '@/services/api/venda.service';
import { formatCurrency, formatDate } from '@/utils/format';
import Button from '@/components/ui/Button';
import ButtonCard from '../ui/button/ButtonCard';

interface VendaCardProps {
  venda: Venda;
  onEdit: () => void;
  onDelete: () => void;
  onViewInvoices: () => void;
}

const getStatusColor = (status: StatusPagamento) => {
  switch (status) {
    case 'PAGO':
      return 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400';
    case 'PENDENTE':
      return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400';
    default:
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-400';
  }
};

const getStatusText = (status: StatusPagamento) => {
  switch (status) {
    case 'PAGO':
      return 'Pago';
    case 'PENDENTE':
      return 'Pendente';
    default:
      return status;
  }
};

const VendaCard: React.FC<VendaCardProps> = ({
  venda,
  onEdit,
  onDelete,
  onViewInvoices
}) => {
  const [showDetails, setShowDetails] = useState(false);

  return (
    <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-5 hover:shadow-lg transition-shadow duration-200">
      {/* Header */}
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            Venda #{venda.id}
          </h3>
          <div className="flex items-center gap-2 mb-2">
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(venda.status)}`}>
              {getStatusText(venda.status)}
            </span>
            <span className="text-sm text-gray-500 dark:text-gray-400">
              {venda.uuid.slice(0, 8)}...
            </span>
          </div>
          
          {/* Cliente e Vendedor */}
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-2 text-sm">
            <div>
              <span className="text-gray-500 dark:text-gray-400">Cliente: </span>
              <span className="font-medium text-gray-900 dark:text-white">
                {venda.cliente?.nome || `ID: ${venda.clienteId}`}
              </span>
            </div>
            <div>
              <span className="text-gray-500 dark:text-gray-400">Vendedor: </span>
              <span className="font-medium text-gray-900 dark:text-white">
                {venda.vendedor?.nome || `ID: ${venda.vendedorId}`}
              </span>
            </div>
          </div>
        </div>
        <div className="flex gap-2 ml-4">
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onViewInvoices}
              title="Ver Faturas"
              icon="eye"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar Venda"
              icon="edit"
            />
            <ButtonCard
              title="Excluir Venda"
              icon="delete"
              variant="secondary"
              size="xs"
              onClick={onDelete}
            />
          {/* <Button
            variant="secondary"
            size="sm"
            onClick={onEdit}
            className="p-2"
          >
            <svg className="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
              <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
            </svg>
          </Button>
          <Button
            variant="danger"
            size="sm"
            onClick={onDelete}
            className="p-2"
          >
            <svg className="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
            </svg>
          </Button> */}
        </div>
      </div>

      {/* Financial Info */}
      <div className="grid grid-cols-2 gap-4 mb-4">
        <div>
          <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Valor Unit√°rio</dt>
          <dd className="text-lg font-semibold text-gray-900 dark:text-white">
            {formatCurrency(venda.valor)}
          </dd>
        </div>
        <div>
          <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Valor Total</dt>
          <dd className="text-lg font-semibold text-green-600 dark:text-green-400">
            {formatCurrency(venda.valorTotal)}
          </dd>
        </div>
      </div>

      {/* Period Info */}
      <div className="mb-4">
        <div className="grid grid-cols-2 gap-4 mb-3">
          <div>
            <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Data de In√≠cio</dt>
            <dd className="text-sm text-gray-900 dark:text-white">
              {formatDate(venda.inicio)}
            </dd>
          </div>
          <div>
            <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Data de Fim</dt>
            <dd className="text-sm text-gray-900 dark:text-white">
              {formatDate(venda.fim)}
            </dd>
          </div>
        </div>
        
        {/* Recorr√™ncia */}
        {venda.quantidadeRecorrencia > 0 && (
          <div className="p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
            <h4 className="text-sm font-medium text-blue-900 dark:text-blue-300 mb-2">
              üìÖ Venda Recorrente
            </h4>
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div>
                <span className="text-blue-700 dark:text-blue-400">Intervalo: </span>
                <span className="font-medium text-blue-900 dark:text-blue-200">
                  {venda.recorrencia} dias
                </span>
              </div>
              <div>
                <span className="text-blue-700 dark:text-blue-400">Repeti√ß√µes: </span>
                <span className="font-medium text-blue-900 dark:text-blue-200">
                  {venda.quantidadeRecorrencia}
                </span>
              </div>
            </div>
            <div className="mt-2 text-xs text-blue-700 dark:text-blue-400">
              Total de cobran√ßas: {venda.quantidadeRecorrencia + 1}
            </div>
          </div>
        )}
      </div>

      {/* Gateway Info */}
      {venda.gateway && (
        <div className={`mb-${venda.produtos && venda.produtos.length > 0 ? '4' : '0'} p-3 bg-gray-50 dark:bg-gray-700 rounded-lg`}>
          <div className="text-sm">
            <span className="text-gray-500 dark:text-gray-400">Gateway: </span>
            <span className="font-medium text-gray-900 dark:text-white">
              {venda.gateway.nome}
            </span>
          </div>
        </div>
      )}

      {/* Products Section */}
      {venda.produtos && venda.produtos.length > 0 && (
        <div className="mb-0">
          <div className="flex items-center justify-between mb-2">
            <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300">
              Produtos ({venda.produtos.length})
            </h4>
            <Button
              variant="secondary"
              size="sm"
              onClick={() => setShowDetails(!showDetails)}
              className="text-xs"
            >
              {showDetails ? 'Ocultar' : 'Mostrar'}
            </Button>
          </div>
          
          {showDetails && (
            <div className="space-y-2">
              {venda.produtos?.map((vendaProduto) => (
                <div key={vendaProduto.id} className="bg-gray-50 dark:bg-gray-700 rounded p-3">
                  <div className="space-y-2">
                    <div className="font-medium text-gray-900 dark:text-white">
                      {vendaProduto.produto.nome}
                    </div>
                    <div className="grid grid-cols-3 gap-2 text-sm">
                      <div>
                        <span className="text-gray-500 dark:text-gray-400">Qtd:</span>
                        <span className="ml-1 text-gray-900 dark:text-white">
                          {vendaProduto.quantidade}
                        </span>
                      </div>
                      <div>
                        <span className="text-gray-500 dark:text-gray-400">Unit:</span>
                        <span className="ml-1 text-gray-900 dark:text-white">
                          {formatCurrency(vendaProduto.valorUnitario)}
                        </span>
                      </div>
                      <div>
                        <span className="text-gray-500 dark:text-gray-400">Total:</span>
                        <span className="ml-1 font-medium text-green-600 dark:text-green-400">
                          {formatCurrency(vendaProduto.valorTotal)}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Actions */}
      {/* <div className="pt-4 border-t border-t-gray-200 dark:border-gray-700">
        <div className="flex gap-2">
          <Button
            variant="primary"
            size="sm"
            onClick={onViewInvoices}
            className="flex-1"
          >
            Ver Faturas
          </Button>
        </div>
      </div> */}
    </div>
  );
};

export default VendaCard;
