import React, { useState, useEffect } from 'react';
import { Venda, CreateVendaRequest, vendaService, TipoPagamento, StatusPagamento } from '@/services/api/venda.service';
import { clienteService, Cliente } from '@/services/api/cliente.service';
import { produtoService, Produto } from '@/services/api/produto.service';
import { gatewayService, Gateway } from '@/services/api/gateway.service';
import { vendedorService, Vendedor } from '@/services/api/vendedor.service';
import { conexaoService, Conexao } from '@/services/api/conexao.service';
import { authService } from '@/services/api/auth.service';
import Button from '@/components/ui/Button';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Select, { SelectOption } from '@/components/ui/Select';
import { formatCurrency, formatCurrencyInput, parseCurrencyInput } from '@/utils/format';
import { toast } from 'react-hot-toast';
import DatePicker from '../form/date-picker';

interface VendaProdutoForm {
  produtoId: number;
  quantidade: number;
}

type VendaFormProps = {
  venda?: Venda | null;
  onSubmit: (data: CreateVendaRequest) => Promise<void>;
  onCancel?: () => void;
};

const tipoOptions: SelectOption[] = [
  { value: 'PIX', label: 'PIX' },
  { value: 'BOLETO', label: 'Boleto' }
];

const statusOptions: SelectOption[] = [
  { value: 'PENDENTE', label: 'Pendente' },
  { value: 'PAGO', label: 'Pago' }
];

const VendaForm: React.FC<VendaFormProps> = ({
  venda,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateVendaRequest>({
    valor: venda?.valor || 0,
    valorTotal: venda?.valorTotal || 0,
    inicio: venda?.inicio ? new Date(venda.inicio) : new Date(),
    clienteId: venda?.clienteId || 0,
    vendedorId: venda?.vendedorId || 0,
    type: (venda as any)?.type || 'PIX',
    produtos: venda?.produtos || [],
    status: venda?.status || 'PENDENTE',
    recorrencia: venda?.recorrencia || 0,
    quantidadeRecorrencia: venda?.quantidadeRecorrencia || 0,
    gatewayId: (venda as any)?.gatewayId || undefined,
    conexaoId: (venda as any)?.conexaoId || undefined
  });

  // Estados para dados din√¢micos
  const [clientes, setClientes] = useState<Cliente[]>([]);
  const [produtos, setProdutos] = useState<Produto[]>([]);
  const [gateways, setGateways] = useState<Gateway[]>([]);
  const [conexoes, setConexoes] = useState<Conexao[]>([]);
  const [vendedores, setVendedores] = useState<Vendedor[]>([]);
  const [currentUser, setCurrentUser] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [valorFormatado, setValorFormatado] = useState(
    venda?.valor ? formatCurrency(venda.valor).replace('R$', '').trim() : ''
  );

  // Verificar se o usu√°rio atual √© um vendedor
  const isVendedor = currentUser?.role === 'VENDEDOR';

  // Carregar dados iniciais
  useEffect(() => {
    const loadInitialData = async () => {
      try {
        setIsLoading(true);

        // Primeiro carrega os dados do usu√°rio
        const userRes = await authService.getMe();
        setCurrentUser(userRes.data);

        // Carrega dados b√°sicos
        const [clientesRes, produtosRes, gatewaysRes, conexoesRes] = await Promise.all([
          clienteService.list({ limit: 100 }),
          produtoService.list({ limit: 100 }),
          gatewayService.list({ limit: 100 }),
          conexaoService.list({ limit: 100 })
        ]);

        setClientes(clientesRes.data || []);
        setProdutos(produtosRes.data || []);
        setGateways(gatewaysRes.data || []);
        setConexoes(conexoesRes.data || []);

        // Se n√£o √© vendedor, carrega lista de vendedores
        if (userRes.data?.role !== 'VENDEDOR') {
          try {
            const vendedoresRes = await vendedorService.list({ limit: 100 });
            setVendedores(vendedoresRes.data || []);
          } catch (error) {
            console.error('Erro ao carregar vendedores:', error);
          }
        }

        // Se n√£o est√° editando, definir vendedorId
        if (!venda && userRes.data) {
          // Se √© vendedor, usa o pr√≥prio ID
          if (userRes.data.role === 'VENDEDOR') {
            setFormData(prev => ({ ...prev, vendedorId: userRes.data.id }));
          }
          // Se n√£o √© vendedor e h√° vendedores dispon√≠veis, n√£o define automaticamente
          // (usu√°rio deve selecionar)
        }
      } catch (error) {
        console.error('Erro ao carregar dados:', error);
        toast.error('Erro ao carregar dados necess√°rios');
      } finally {
        setIsLoading(false);
      }
    };

    loadInitialData();
  }, [venda]);

  // Calcular valor total automaticamente
  useEffect(() => {
    let valorCalculado = formData.valor;
    if (formData.recorrencia && formData.recorrencia > 0 && formData.quantidadeRecorrencia && formData.quantidadeRecorrencia > 0) {
      // Valor total = valor unit√°rio √ó (quantidade de recorr√™ncias + 1)
      // +1 porque al√©m das recorr√™ncias, h√° a primeira cobran√ßa
      valorCalculado = formData.valor * (formData.quantidadeRecorrencia + 1);
    }

    if (valorCalculado !== formData.valorTotal) {
      setFormData(prev => ({ ...prev, valorTotal: valorCalculado }));
    }
  }, [formData.valor, formData.recorrencia, formData.quantidadeRecorrencia, formData.valorTotal]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.inicio) {
      toast.error('Data de in√≠cio √© obrigat√≥ria');
      return;
    }

    if (!formData.clienteId || formData.clienteId <= 0) {
      toast.error('Cliente √© obrigat√≥rio');
      return;
    }

    if (!formData.vendedorId || formData.vendedorId <= 0) {
      toast.error('Vendedor √© obrigat√≥rio');
      return;
    }

    if (!formData.valor || formData.valor <= 0) {
      toast.error('Valor deve ser maior que zero');
      return;
    }

    const gatewayDefault = gateways.find(gateway => gateway.isDefault);

    if (!formData.gatewayId && !gatewayDefault) {
      toast.error('Gateway de pagamento √© obrigat√≥rio quando n√£o h√° gateway padr√£o');
      return;
    }

    // Valida√ß√µes espec√≠ficas para recorr√™ncia
    if (formData.recorrencia && formData.recorrencia > 0) {
      if (!formData.quantidadeRecorrencia || formData.quantidadeRecorrencia < 1) {
        toast.error('Quantidade de recorr√™ncias deve ser pelo menos 1');
        return;
      }
      if (formData.recorrencia < 1) {
        toast.error('Intervalo de recorr√™ncia deve ser pelo menos 1 dia');
        return;
      }
    }

    if (formData.produtos && formData.produtos.some(p => !p.produtoId || !p.quantidade)) {
      toast.error('Todos os campos dos produtos s√£o obrigat√≥rios');
      return;
    }

    try {
      setIsSubmitting(true);
      await onSubmit(formData);
    } catch (error) {
      console.error('Erro ao salvar venda:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const addProduto = () => {
    const newProdutos = [...(formData.produtos || []), { produtoId: 0, quantidade: 1 }];
    setFormData(prev => ({
      ...prev,
      produtos: newProdutos
    }));
  };

  const removeProduto = (index: number) => {
    const newProdutos = (formData.produtos || []).filter((_, i) => i !== index);
    setFormData(prev => ({
      ...prev,
      produtos: newProdutos
    }));
  };

  const updateProduto = (index: number, field: keyof VendaProdutoForm, value: any) => {
    const newProdutos = [...(formData.produtos || [])];
    newProdutos[index] = { ...newProdutos[index], [field]: value };
    setFormData(prev => ({
      ...prev,
      produtos: newProdutos
    }));
  };

  // Op√ß√µes para os selects
  const clienteOptions: SelectOption[] = clientes.map(cliente => ({
    value: cliente.id.toString(),
    label: `${cliente.nome} ${cliente.cpfCnpj ? `(${cliente.cpfCnpj})` : ''}`
  }));

  const produtoOptions: SelectOption[] = produtos.map(produto => ({
    value: produto.id.toString(),
    label: `${produto.nome} - ${formatCurrency(produto.valor)}`
  }));

  const gatewayOptions: SelectOption[] = gateways.map(gateway => ({
    value: gateway.id.toString(),
    label: `${gateway.nome} (${gateway.gateway}) ${gateway.isDefault ? '(Padr√£o)' : ''}`
  }));

  const conexaoOptions: SelectOption[] = conexoes.map(conexao => ({
    value: conexao.id.toString(),
    label: `${conexao.nome} ${conexao.isDefault ? '(Padr√£o)' : ''}`
  }));

  const vendedorOptions: SelectOption[] = vendedores.map(vendedor => ({
    value: vendedor.id.toString(),
    label: `${vendedor.nome} (${vendedor.email})`
  }));

  if (isLoading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
          <p>Carregando dados...</p>
        </div>
      </div>
    );
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Cliente */}
        <div>
          <Label htmlFor="clienteId">Cliente *</Label>
          <Select
            value={clienteOptions.find(opt => opt.value === formData.clienteId.toString())}
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData(prev => ({
                ...prev,
                clienteId: selectedOption ? parseInt(selectedOption.value) : 0
              }));
            }}
            options={clienteOptions}
            placeholder="Selecione um cliente"
            isClearable={false}
          />
        </div>

        {/* Vendedor */}
        <div>
          <Label htmlFor="vendedor">Vendedor *</Label>
          {isVendedor ? (
            // Se √© vendedor, mostra campo readonly com nome do usu√°rio atual
            <Input
              id="vendedor"
              type="text"
              value={currentUser ? `${currentUser.nome} (${currentUser.email})` : 'Carregando...'}
              readOnly
              className="bg-gray-50"
            />
          ) : (
            // Se n√£o √© vendedor, mostra select de vendedores
            <Select
              value={vendedorOptions.find(opt => opt.value === formData.vendedorId?.toString())}
              onChange={(option) => {
                const selectedOption = option as SelectOption;
                setFormData(prev => ({
                  ...prev,
                  vendedorId: selectedOption ? parseInt(selectedOption.value) : 0
                }));
              }}
              options={vendedorOptions}
              placeholder="Selecione um vendedor"
              isClearable={false}
            />
          )}
        </div>

        {/* Tipo */}
        <div>
          <Label htmlFor="type">Tipo *</Label>
          <Select
            value={tipoOptions.find(opt => opt.value === formData.type)}
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData(prev => ({
                ...prev,
                type: (selectedOption?.value as TipoPagamento) || 'PIX'
              }));
            }}
            options={tipoOptions}
            isClearable={false}
          />
        </div>

        {/* Valor */}
        <div>
          <Label htmlFor="valor">Valor *</Label>
          <div className="relative">
            <span className="absolute z-1 left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-300">
              R$
            </span>
            <Input
              id="valor"
              type="text"
              className="pl-9"
              value={valorFormatado}
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input === '') {
                  setValorFormatado('');
                  setFormData(prev => ({ ...prev, valor: 0 }));
                  return;
                }

                const formatted = formatCurrencyInput(input);
                setValorFormatado(formatted);
                setFormData(prev => ({ ...prev, valor: parseCurrencyInput(input) }));
              }}
              placeholder="0,00"
              required
            />
          </div>
        </div>

        {/* Data de In√≠cio */}
        <div>
          <Label htmlFor="inicio">Data de In√≠cio *</Label>
          {/* <Input
              id="inicio"
              type="date"
              value={formData.inicio}
              onChange={(e) => setFormData(prev => ({ ...prev, inicio: e.target.value }))}
              required
            /> */}
          <DatePicker
            id="data"
            onChange={(selectedDates: Date[]) => {
              if (selectedDates[0]) {
                setFormData({
                  ...formData,
                  inicio: selectedDates[0]
                });
              }
            }}
            defaultDate={new Date(formData.inicio || new Date())}
          />
        </div>

        {/* Status */}
        <div>
          <Label htmlFor="status">Status *</Label>
          <Select
            value={statusOptions.find(opt => opt.value === formData.status)}
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData(prev => ({
                ...prev,
                status: (selectedOption?.value as StatusPagamento) || 'PENDENTE'
              }));
            }}
            options={statusOptions}
            isClearable={false}
          />
        </div>

        {/* Gateway */}
        <div>
          <Label htmlFor="gatewayId">Gateway de Pagamento</Label>
          <Select
            value={gatewayOptions.find(opt => opt.value === formData.gatewayId?.toString())}
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData(prev => ({
                ...prev,
                gatewayId: selectedOption ? parseInt(selectedOption.value) : undefined
              }));
            }}
            options={gatewayOptions}
            placeholder="Selecione um gateway (opcional)"
            isClearable
          />
        </div>

        {/* Conex√£o */}
        <div>
          <Label htmlFor="conexaoId">Conex√£o WhatsApp</Label>
          <Select
            value={conexaoOptions.find(opt => opt.value === formData.conexaoId?.toString())}
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData(prev => ({
                ...prev,
                conexaoId: selectedOption ? parseInt(selectedOption.value) : undefined
              }));
            }}
            options={conexaoOptions}
            placeholder="Selecione uma conex√£o (opcional)"
            isClearable
          />
        </div>
      </div>

      {/* Recorr√™ncia */}
      <div className="space-y-4 p-4 border rounded-lg bg-gray-50 dark:bg-gray-700">
        <div className="flex items-center space-x-2">
          <input
            title="Venda recorrente"
            type="checkbox"
            id="recorrencia"
            checked={(formData.recorrencia || 0) > 0}
            onChange={(e) => setFormData(prev => ({
              ...prev,
              recorrencia: e.target.checked ? 30 : 0,
              quantidadeRecorrencia: e.target.checked ? 1 : 0
            }))}
            className="rounded border-gray-300"
          />
          <Label htmlFor="recorrencia" className="font-semibold">Venda recorrente</Label>
        </div>

        {(formData.recorrencia || 0) > 0 && (
          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="recorrencia">Intervalo (dias) *</Label>
                <Input
                  id="recorrencia"
                  type="number"
                  min={1}
                  value={formData.recorrencia || 30}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    recorrencia: parseInt(e.target.value) || 30
                  }))}
                  placeholder="30"
                />
                <small className="text-gray-600 dark:text-gray-400">Intervalo em dias entre cada cobran√ßa</small>
              </div>

              <div>
                <Label htmlFor="quantidadeRecorrencia">Quantidade de Repeti√ß√µes *</Label>
                <Input
                  id="quantidadeRecorrencia"
                  type="number"
                  min={1}
                  value={formData.quantidadeRecorrencia || 1}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    quantidadeRecorrencia: parseInt(e.target.value) || 1
                  }))}
                  placeholder="1"
                />
                <small className="text-gray-600 dark:text-gray-400">Quantas vezes a cobran√ßa se repetir√° ap√≥s a primeira</small>
              </div>
            </div>

            {/* Preview das datas de cobran√ßa */}
            {formData.inicio && formData.recorrencia && formData.quantidadeRecorrencia && (
              <div className="mt-4 p-3  border rounded-lg">
                <h4 className="font-medium text-gray-600 dark:text-gray-400 mb-2">Cronograma de Cobran√ßas:</h4>
                <div className="space-y-1">
                  {Array.from({ length: formData.quantidadeRecorrencia + 1 }, (_, index) => {
                    const dataCobranca = new Date(formData.inicio);
                    dataCobranca.setDate(dataCobranca.getDate() + (index * (formData.recorrencia || 30)));
                    return (
                      <div key={index} className="text-sm text-gray-700 dark:text-gray-500">
                        <span className="font-medium">{index + 1}¬™ cobran√ßa:</span> {dataCobranca.toLocaleDateString('pt-BR')}
                        {index === 0 && <span className="text-green-600 ml-2">(Primeira cobran√ßa)</span>}
                      </div>
                    );
                  })}
                </div>
                <div className="mt-2 text-sm text-gray-600 dark:text-gray-400">
                  <strong>Total de cobran√ßas:</strong> {formData.quantidadeRecorrencia + 1} ({formatCurrency((formData.quantidadeRecorrencia + 1) * formData.valor)})
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Produtos */}
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <Label>Produtos (opcional)</Label>
          <Button
            type="button"
            onClick={addProduto}
            variant="secondary"
            size="sm"
          >
            Adicionar Produto
          </Button>
        </div>

        {formData.produtos && formData.produtos.map((produto, index) => (
          <div key={index} className="flex gap-4 items-end p-4 border rounded-lg">
            <div className="flex-1">
              <Label htmlFor={`produto-${index}`}>Produto</Label>
              <Select
                value={produtoOptions.find(opt => opt.value === produto.produtoId.toString())}
                onChange={(option) => {
                  const selectedOption = option as SelectOption;
                  updateProduto(index, 'produtoId', selectedOption ? parseInt(selectedOption.value) : 0);
                }}
                options={produtoOptions}
                placeholder="Selecione um produto"
                isClearable={false}
              />
            </div>

            <div className="w-32">
              <Label htmlFor={`quantidade-${index}`}>Quantidade</Label>
              <Input
                id={`quantidade-${index}`}
                type="number"
                min={1}
                value={produto.quantidade}
                onChange={(e) => updateProduto(index, 'quantidade', parseInt(e.target.value) || 1)}
              />
            </div>

            <Button
              type="button"
              onClick={() => removeProduto(index)}
              variant="danger"
              size="sm"
            >
              Remover
            </Button>
          </div>
        ))}
      </div>

      {/* Valor Total (readonly) */}
      <div>
        <Label htmlFor="valorTotal">Valor Total</Label>
        <Input
          id="valorTotal"
          type="text"
          value={formatCurrency(formData.valorTotal)}
          readOnly
          className="bg-gray-50"
        />
      </div>

      {/* Bot√µes */}
      <div className="flex gap-4 pt-6">
        {onCancel && (
          <Button
            type="button"
            variant="secondary"
            onClick={onCancel}
            className="flex-1"
          >
            Cancelar
          </Button>
        )}
        <Button
          type="submit"
          disabled={isSubmitting}
          variant="primary"
          className="flex-1"
        >
          {isSubmitting ? 'Salvando...' : (venda ? 'Atualizar' : 'Criar Venda')}
        </Button>

      </div>
    </form>
  );
};

export default VendaForm;